* {
  margin: 0;
  padding: 0;

}


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.511);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  width: 100vw;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
}

.flex {
  display: flex;
  justify-content: center;
  align-items: center;
}

.heading {
  position: absolute;
  top: 30px;
  font-size: 3rem;
  color: white;
  text-align: center;
  margin: auto;
}

.bodyContainer {
  width: fit-content;
  height: fit-content;
  background-color: white;
  /* background-color: rgb(198, 238, 238); */
  flex-direction: column;
  border: 3px solid rgb(148, 107, 107);
  /* box-shadow: 2px 2px 100px 30px pink inset; */
  border-radius: 12px;
  padding: 40px;
}


.box {
  width: 80px;
  height: 80px;
  margin: 13px;
  font-size: 40px;
  font-weight: 600;
  color: aliceblue;
  border-radius: 12px;
  cursor: pointer;
  background-color: rgba(158, 152, 152, 0.862);

}



.overBodyContainer {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 2000;

}

.overContainer {
  width: 100%;
  padding: 10px 0;
  flex-direction: column;
  color: whitesmoke;
  box-shadow: 2px 2px 50px 10px grey;
  background-color: rgba(76, 69, 69, 0.962);
}

.overContainer h1 {
  font-size: 48px;
}

.overContainer h3 {
  font-size: 32px
}

.overContainer button {
  width: 200px;
  height: 40px;
  border-radius: 12px;
  border: 2px solid rgba(158, 152, 152, 0.862);
  background-color: rgba(70, 75, 75, 0.496);
  color: whitesmoke;
  font-size: 22px;
  text-align: center;
  cursor: pointer;
  padding: 10px;
  margin: 30px;
}

#continuebtn {
  background-color: rgba(3, 159, 3, 0.57);
}

#newgamebtn {
  background-color: rgb(79, 177, 163);
}

.overContainer button:hover,
#continuebtn:hover,
#newgamebtn:hover {
  background-color: rgba(70, 75, 75, 0.496);
}

#X {
  background-color: red;
}

#O {
  background-color: blue;
}

#X1 {
  color: red;
}

#O1 {
  color: blue;
}


.gamewinner {
  position: absolute;
  bottom: 20px;
  left: 0;
  width: 99vw;
  height: 80px;
}

.gamewinnercontainer {
  width: 65%;
  height: 100%;
  border-radius: 12px;
}

.circle {
  min-width: 25px;
  max-width: 25px;
  max-height: 25px;
  min-height: 25px;
  background-color: rgb(255, 255, 255);
  color: white;
  margin: 20px 50px;
  padding: 30px;
  border-radius: 50%;
  font-size: 40px;
  font-weight: 600;
  text-align: center;
}

.turnshow {
  position: fixed;
  top: 5px;
  right: 20px;
  font-size: 38px;
  font-weight: 600;
  color: whitesmoke;
}

#Xturn {
  top: 5px;
  right: 20px;
}

#Oturn {
  top: 5px;
  left: 20px;
}

span#X1 {
  margin-right: 50px;
}





@media screen and (max-width: 380px) and (min-width:0px) {

  .heading
  {
    font-size: 40px;
  }

  .box {
    width: 60px;
    height: 60px;
    margin: 10px;
    font-size: 20px;
    border-radius: 12px;

  }


  .bodyContainer {
    padding: 20px;
    translate: 0 -5%;
  }

  .circle {
    min-width: 10px;
    max-width: 10px;
    max-height: 10px;
    min-height: 10px;
    margin: 0px 10px;
    padding: 30px;
    font-size: 20px;
    font-weight: 600;
  }

  .overBodyContainer
  {
    justify-content: center;
    align-items: start;
    left: initial;
    height: 100%;
  }

  .overContainer
  {
    width: 100%;
    height: 100%;
  }


  .gamewinner
  {
    bottom: 30px;
  }

  .gamewinnercontainer
  {
    width: 100vw;
    overflow-x: scroll;
    justify-content: start;
  }

}

@media only screen and (max-width: 450px) and (min-width:380px) {


  .box {
    width: 72px;
    height: 72px;
    margin: 10px;
    font-size: 40px;
    border-radius: 12px;
  }

  .bodyContainer {
    padding: 20px;
  }

  .circle {
    min-width: 20px;
    max-width: 20px;
    max-height: 20px;
    min-height: 20px;
    margin: 0px 10px;
    padding: 25px;
    font-size: 30px;
    font-weight: 600;
  }

  .gamewinner
  {
    bottom: 20px;
  }

  .gamewinnercontainer
  {
    width: 100vw;
    overflow-x: scroll;
    justify-content: start;
  }

}


@media only screen and (max-width: 780px) and (min-width:450px) {

  .box {
    width: calc(30vw/3 );
    height: calc(30vw/3);
    margin: 10px;
    font-size: 40px;
    border-radius: 12px;
  }

  .bodyContainer {
    padding: 30px;
  }

  .circle {
    margin: 0px 20px;
    padding: 30px;
    font-size: 50px;
    font-weight: 600;
  }

  .gamewinner
  {
    bottom: 8px;
  }

  .overContainer
  {
    height: calc(70vw);
  }

  .overContainer h1
  {
    font-size: 60px;
  }

  #continuebtn, #newgamebtn
  {
    padding: 40px;
  }

}
